export async function loadTaxon(id) {
  const responce = await fetch(
    "https://api.jsonstorage.net/v1/json/db50a6ad-b4ca-4244-915e-579a84c30645/bfe016a2-dcaf-431d-a89b-10a8596cebe4"
  );
  const taxons = await responce.json();
  const taxon = await taxons.find((t) => t.name === id);
  return { taxon };
}

