<form><h3 class="text-primary fs-1">Вопрос <strong>№ {{ quest.id }}</strong></h3><p class="fs-5">{{ quest.text }}</p><container class="form-check" v-for="(ans, index) in quest.answers"><input class="form-check-input" type="radio" name="index" :value="ans.id" v-model="answerID" @change="submit()" :id="ans.text"><label class="form-check-label fs-6 fw-light" :for="ans.text">{{ ans.text }}</label></container><hr></form>