<transition name="fade"><test-finish-note v-if="finished" @reset="reset" :scores="scores" :maxScores="maxScores"></test-finish-note></transition><transition name="fade"> <loading-card v-if="!quests"><h1>Загружается тест...</h1></loading-card></transition><transition name="fade"> <form v-if="!finished &amp;&amp; quests"><br><section v-for="quest in quests"><quest-radio @submit.primary="setUserAnswer" :quest="quest"></quest-radio></section><br><button class="btn btn-primary mb-2" type="submit" @click="submit">Проверить тест</button></form></transition><br>